.gallery {
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

img {
  width: 100%;
  height: auto;
  max-width: 300px;
  display: block;
}

.gallery__title {
  text-align: center;
  color: #202025;
  font-size: 48px;
  font-weight: 700;
  line-height: 72px;
}

#pagination {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.prev, .next {
  width: 60px;
  height: 40px;
  color: #fe7031;
  cursor: pointer;
  background: #fdfdfd;
  border: 1px solid #eaebea;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.pagination-number {
  gap: 20px;
  display: flex;
}

.is-hidden {
  opacity: 0;
}

.btn-pagination {
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  color: #fe7031;
  cursor: pointer;
  background: #fdfdfd;
  border: 1px solid #eaebea;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding: 0;
  line-height: 20px;
}

.active {
  color: #fdfdfd;
  background-color: #fe7031;
  border: none;
}

